import React from 'react'
import ProjectCard from './ProjectCard'
import demo1 from './img/demo1.png'
import demo2 from './img/demo2.png'
import demo3 from './img/demo3.png'
import demo4 from './img/demo4.png'

export default function Project() {
    return (
        
        <section>
            <div className='container pt-5 mt-5'>
                <h3 className='mt-5'>Projects</h3>
                <hr/>

                <div className='row my-4 '>
                    <div className='col-md-4'>
                        <ProjectCard name={'Demo project 1'} image={demo1} button={'btn btn-primary btn-projects'} link={'https://iqbal221.github.io/arhamshop/'}/>
                    </div>

                    <div className='col-md-4 '>
                        <ProjectCard name={'Demo project 2'} image={demo2} button={'btn btn-success btn-projects'} link={' https://iqbal221.github.io/zawadithouse/'} />  
                    </div>

                    <div className='col-md-4 '>
                        <ProjectCard name={'Demo project 3'} image={demo3} button={'btn btn-danger btn-projects'} link={'https://iqbal221.github.io/zawadithome/'}/>  
                    </div>
                </div>

                <div className='row my-4'>
                    <div className='col-md-4'>
                        <ProjectCard name={'Demo project 4'} image={demo4} button={'btn btn-info btn-projects'} link={'https://iqbal221.github.io/zahediqbalportfolio/'}/>
                    </div>

                    <div className='col-md-4'>
                        <ProjectCard name={'Demo project 5'} image={demo1} button={'btn btn-warning btn-projects'}/>  
                    </div>
                </div>
            </div>

            
        </section>
    )
}
