
import React from 'react';

export default function PersonalInfoCard(props) {
  return (
    <div>
        <h4>{props.PersonalInfo}</h4>
        <div className=' row mb-4  personal' >
            <div className=
            'col-md-4 col-sm-4 col-5  ms-5'><h6 >{props.Name1}</h6></div>
            <div className='col-md-4 col-sm-6 col-5 fs-5 '><h6 className='name-2' >{props.Name2}</h6></div>
        </div>
    </div>
  
  );  

}