
import React from 'react';

export default function EducationCard(props) {
  return (
    <div>
        <h4>{props.name}</h4>
        <div className=' row  d-flex ms-2 mb-4 text-center education' >
            <div className='col-md-4 col-sm-4 col-5 '><h6 >{props.year}</h6></div>
            <div className='col-md-5 col-sm-8 col-5'>
              <h6 className='fs-5'>{props.certificate}</h6>
              <h6 >{props.subject}</h6>
              <h6 >{props.result}</h6>
            </div>
            
        </div>
    </div>
     
  );  
                         
}
