import React from 'react'
import { NavLink } from 'react-router-dom'
import './App.css'

export default function Navbar() {
    return (
        <nav className='navbar navbar-expand-md  bg-light navbar-light fixed-top py-4'> 
            <div className='container-fluid mx-4 '>
                <NavLink className='navbar-brand' to='/'>Zahed Iqbal</NavLink>
                <button className='navbar-toggler ' type='button' data-bs-toggle='collapse' data-bs-target='#nav_menu'>
                    <span className='navbar-toggler-icon '></span>
                </button>
                <div className='collapse navbar-collapse' id='nav_menu'>
                    <ul className='navbar-nav ms-auto'>
                        <li className='nav-item'><NavLink  className='nav-link' to='/'>ABOUT ME</NavLink></li>
                        <li className='nav-item'><NavLink activeClassName='menu_active' className='nav-link' to='/resume'>RESUME</NavLink></li>
                        <li className='nav-item'><NavLink activeClassName='menu_active' className='nav-link' to='/project'>PROJECTS</NavLink></li>
                        <li className='nav-item'><NavLink activeClassName='menu_active' className='nav-link' to='/contact'>CONTACT</NavLink></li>
                    </ul>
                </div>
            </div>
        </nav>
    )
}

