
import React,{useRef} from 'react';
import emailjs from '@emailjs/browser';
import ContactCard from './ContactCard';
import ContactFormCard from './ContactFormCard';

export default function Contact() {

    const form = useRef();

    const sendEmail = (e) => {
      e.preventDefault();
  
      emailjs.sendForm('service_hya87e6', 'template_plzj4su', form.current, 'user_iT4rjjIubf9xtNhDpO2CL')
        .then((result) => {
            console.log(result.text);
        }, (error) => {
            console.log(error.text);
        });
        alert('Send Email');
    };

    return (
        
        <section>

            <div className='container pt-5 mt-5'>
                <h3 className='mt-5'>Contact Me</h3>
                <hr/>
                <div className='row'>
                    <div className='col-md-4  m-auto'>
                        
                        <ContactCard name={'Phone'} number={'01819832618'} />
                        <ContactCard name={'Email'} number={'zahediiuc221@gmail.com'} />
                    
                    </div>

                    <div className='col-md-5 my-2 mx-auto'>

                        <form ref={form} onSubmit={sendEmail}> 
                            
                            <ContactFormCard for={'name'} Name={'Name'} type={'text'} Name1={'name'} id={'name'} placeholder={'Enter your name'} />
                            <ContactFormCard for={'email'} Name={'Email'} type={'email'} Name1={'email'} id={'email'} placeholder={'Enter valid email'} />
                            <ContactFormCard for={'subject'} Name={'Subject'} type={'subject'} Name1={'subject'} id={'subject'} placeholder={'Enter your subject'} />

                            <div>
                                <label htmlFor='message'>Textarea </label>
                                <textarea name='message' id='textArea' rows='6' placeholder='Message here' className='form-control' required></textarea>
                            </div>

                            <button type='submit' className='btn btn-primary px-4 my-3 contact-btn'>Submit</button>
                        </form>
  
                    </div>
                    
                </div>
            </div>
        </section>
        
    );
}
