import React from 'react';


export default function ProjectCard(props) {
  return  (
            <div>
                <div class="card project-card mx-auto mb-4" >
                    <img src={props.image} className="card-img-top" alt="..."/>
                    <div className="card-body">
                        <h5 className="card-title my-4">{props.name}</h5>
                        <a href={props.link} target='_blank' rel="noreferrer" className={props.button}>Project link</a>
                    </div>
                </div>
            </div>
  );
}
