import React from 'react'
import './App.css';
import EducationCard from './EducationCard';
import EducationData from './EducationData'
import SkillCard from './SkillCard'
import html from './img/html.png'
import css from './img/css.png'
import bootstrap from './img/bootstrap.png'
import javascript from './img/javascript.jpg'
import jquery from './img/jquery.png'
import reactjs from './img/react.png'
import nodejs from './img/nodejs.png'
import expressjs from './img/expressjs.png'
import git from './img/git.png'
import github from './img/github.jpg'
import mongodb from './img/mongodb.png'
import gitbash from './img/gitbash.jpg'
import PersonalInfoCard from './PersonalInfoCard';


export default function Resume() {
    return (
        <section>
            <div className='container pt-5 mt-5'>
                <h3 className='mt-5'>Resume</h3>
                <hr/>

{/* ...............................My Skills............................................ */}
 
                <div className='row px-2'>
                    <div className='col-md-4 col-sm-12 mt-3 ms-4'>
                        <h4>My Skills</h4>
                    </div>

                    <div className=' col-md-7 col-sm-12 mb-5 mt-2 mx-auto skill-card'>
                
                            <SkillCard image1={html} image2={css} image3={bootstrap} image4={javascript} SkillName1={'HTML5'}  SkillName2={'CSS3'} SkillName3={'BOOTSTRAP5'} SkillName4={'JAVASCRIPT'} />
                            <SkillCard image1={jquery} image2={reactjs} image3={nodejs} image4={expressjs}  SkillName1={'JQUERY'}  SkillName2={'REACT JS'} SkillName3={'NODE JS'} SkillName4={'EXPRESS JS'} />
                            <SkillCard image1={mongodb} image2={github} image3={git} image4={gitbash}  SkillName1={'MONGO DB'} SkillName2={'GIT'}  SkillName3={'GITHUB'} SkillName4={'GIT BASH'} />
                       
                    </div>
                </div>
                <hr/>
 {/* ...............................Educational Background............................................ */}
                
                <div className='row px-2 mt-4 '>
                    <div className='col-md-4 col-sm-12 ms-3'>
                        <EducationCard name={'Educational Background'} />
                    </div>

                    <div className='col-md-7 col-sm-12  mb-3  mx-auto  education-card '>
                        <div >
                            {
                                EducationData.map((value,index) => {
                                    return (
                                        <EducationCard key={index} certificate={value.certificate} subject = {value.subject} result = {value.result} year = {value.year}/>
                                    )
                                })
                            }
                        </div>
                    </div>
                </div>
                <hr/>



 {/* ...............................Personal Details............................................ */}               

                <div className='row px-2'>
                    <div className='col-md-4 col-sm-12 ms-3 mt-4'>
                        <h4>Personal Information</h4>
                    </div>
                    

                    <div className='col-md-7 col-sm-12 justify-content-center mb-5 mt-4 mx-auto skill-card'>
                        
                        <div>     
                            <PersonalInfoCard Name1={'Name:'} Name2={'Md Zahed Iqbal'} />
                            <PersonalInfoCard Name1={'Father Name:'} Name2={'Nesar Ahmed'} />
                            <PersonalInfoCard Name1={'Mother Name:'} Name2={'Samsun Nahar'} />
                            <PersonalInfoCard Name1={'Birth Date:'} Name2={'September 22, 1985'} />
                            <PersonalInfoCard Name1={'Marital Status:'} Name2={'Married'} />
                            <PersonalInfoCard Name1={'Gender:'} Name2={'Male'} />
                            <PersonalInfoCard Name1={'Nationality:'} Name2={'Bangladeshi'} />
                            <PersonalInfoCard Name1={'Religion:'} Name2={'Islam'} />
                            <PersonalInfoCard Name1={'Current Location:'} Name2={'Chattogram'} />
                        </div>
                    </div>
                </div>

                
            </div>
        </section>
            
              
            
        
    )
}
