
import React from 'react';

export default function SkillCard(props) {
  return (
    <div>
        
        <div className='row text-center ms-1 d-flex mb-4 mt-3 me-3' >
            <div className='col-sm-3 col-3 skill-image1'>
              <img className='mb-2' src={props.image1} alt=''/>
              <p className='skill-name'>{props.SkillName1}</p>
            </div>
            <div className='col-sm-3 col-3 skill-image2 '>
            < img className='mb-2' src={props.image2} alt=''/>
              <p className='skill-name'>{props.SkillName2}</p>
            </div>
            <div className='col-sm-3 col-3 skill-image3 '>
              <img className='mb-2' src={props.image3} alt=''/>
              <p className='skill-name'>{props.SkillName3}</p>
            </div>
            <div className='col-sm-3 col-3 skill-image4'>
              <img className='mb-2' src={props.image4} alt=''/>
              <p className='skill-name'>{props.SkillName4}</p>
            </div>
        </div>
    </div>
     
  );  
                         
}
  
                         
