import React from 'react';


export default function ContactFormCard(props) {


  return (
        <div>
            
                <div className='my-3'>
                    <label htmlFor={props.For}>{props.Name} </label>
                    <input type={props.type} name={props.Name1} id={props.id} placeholder={props.placeholder} className='form-control' required />
                </div>
                
        </div>
    );
}
