import React from 'react'
import { NavLink } from 'react-router-dom'
import Pic from './img/pic2.jpg'

export default function Home() {
    return (
        <div className='container-fluid'>
            <div className='row home pt-5 mt-5'>
                <div className='col-md-6 mt-5 mb-3 py-5 d-flex justify-content-center '>
                    <img className='personal-img' src={Pic} alt=''/>
                </div>

                <div className='my-auto col-md-6 personal-info '>
                    <h1>Hello</h1>
                    <p className='my-4'>I am professional web developer. I can make responsive dynamic website. I have achieved experience about web development since last few years. I am sincere and honest about my profession. Time concern is my strong side. Web developing is my passion. I love my work very much.</p>
                    <div className='pb-3'>
                        <NavLink className='btn btn-primary px-3 me-2 btn-resume' to='/resume'>RESUME</NavLink>
                        <NavLink className='btn btn-primary btn-project' to='/project'>PROJECTS</NavLink>
                    </div>
                </div>
            </div>
           
            
        </div>
    )
}
